/* 页面容器 */
.page-container {
  min-height: 100vh;
  background-color: #f5f7fa;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}

.page-content {
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.1);
  padding: 3rem;
  max-width: 1000px;
  width: 100%;
  text-align: center;
}

.page-content h1 {
  color: #3a7bc8;
  margin-bottom: 1rem;
  font-size: 2.2rem;
}

.page-content p {
  color: #666;
  margin-bottom: 2.5rem;
  font-size: 1.1rem;
}

/* 下载区域 */
.download-section {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-bottom: 3rem;
  flex-wrap: wrap;
}

.download-card {
  background-color: #f9fafc;
  border-radius: 10px;
  border: 1px solid #e0e6ed;
  padding: 2rem;
  width: 250px;
  transition: transform 0.3s, box-shadow 0.3s;
}

.download-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.08);
}

.download-card h2 {
  color: #4a4a4a;
  margin-bottom: 0.7rem;
  font-size: 1.4rem;
}

.download-card p {
  margin-bottom: 1.5rem;
  color: #777;
  font-size: 0.95rem;
}

.download-btn {
  background-color: #4a90e2;
  color: white;
  border: none;
  border-radius: 6px;
  padding: 0.8rem 1.5rem;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.2s;
}

.download-btn:hover {
  background-color: #3a7bc8;
}

/* 视频区域 */
.video-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin-bottom: 3rem;
}

.video-placeholder {
  background-color: #f0f2f5;
  border-radius: 8px;
  width: 280px;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: background-color 0.3s;
  position: relative;
  overflow: hidden;
}

.video-placeholder:hover {
  background-color: #e5e9ef;
}

.video-placeholder:hover .play-icon {
  transform: scale(1.2);
}

.play-icon {
  font-size: 2.5rem;
  color: #4a90e2;
  margin-bottom: 1rem;
  transition: transform 0.3s;
}

.video-placeholder p {
  color: #555;
  font-size: 1rem;
  margin-bottom: 0;
  padding: 0 1rem;
}

/* 返回链接 */
.back-link {
  display: inline-block;
  color: #4a90e2;
  text-decoration: none;
  font-weight: 600;
  transition: color 0.2s;
}

.back-link:hover {
  color: #3a7bc8;
  text-decoration: underline;
}

/* 响应式适配 */
@media (max-width: 768px) {
  .page-content {
    padding: 2rem 1.5rem;
  }
  
  .download-section {
    flex-direction: column;
    align-items: center;
  }
  
  .download-card {
    width: 100%;
    max-width: 300px;
  }
} 